<template>
  <div class="discuss">
    <div class="boxTop">
      <div class="navTopSupport"></div>
      <div class="navTop">
        <div class="navLetf">
          <i @click="$router.go(-1)"
            ><img src="../assets/images/icon/returnKey.png" alt=""/></i
          ><span>评论({{ data.length }})</span>
        </div>
        <!-- <img
          class="navRight"
          src="../assets/images/icon/share.png"
          alt="分享"
        /> -->
      </div>

      <div class="songMess">
        <div class="left">
          <img
            src="https://p1.music.126.net/IUJSxQQTg4LklwNBNvPHaw==/109951165797303159.jpg?param=140y140"
            alt=""
          />
          <div class="centen">
            <div>
              <h3>流行音乐</h3>
              <span>Tie</span>
            </div>
            <!-- <p>></p> -->
          </div>
        </div>
      </div>
    </div>

    <div class="content">
      <div class="top" :class="searchBarFixed == true ? 'isFixed' : ''">
        <h4>评论区</h4>
        <p>
          <span
            @click="(data = commentsUser), (indexActive = 1)"
            :class="indexActive == 1 ? 'active' : ''"
            >推荐</span
          >
          <span
            @click="(data = hotCommentsUser), (indexActive = 2)"
            :class="indexActive == 2 ? 'active' : ''"
            >最热</span
          >
        </p>
      </div>

      <div class="userList">
        <div v-for="item in data" :key="item.commentId" class="item">
          <div class="userName">
            <img class="left" :src="item.user.avatarUrl+'?param=40y40'" alt="" />
            <div class="nickname">
              <p>{{ item.user.nickname }}</p>
              <span>{{ item.time | formatTime }}</span>
            </div>
            <span class="give">
              <i>{{ item.likedCount != 0 ? item.likedCount : "" }}</i>
            </span>
          </div>

          <p class="comment">
            {{ item.content }}
          </p>
        </div>
      </div>
      <Loading v-if="isLoading"></Loading>
    </div>

    <div class="write">
      <form action="">
        <input type="text" name="" id="" placeholder="写评论..." />
        <button>发送</button>
      </form>
    </div>
  </div>
</template>
<style lang="less" scoped>
.discuss {
  position: relative;
  background-color: white;
  height: 100vh;
  z-index: 999;
  .boxTop {
    padding: 3%;
    height: 160px;
    background-color: white;
    width: 100%;
    box-sizing: border-box;
    .navTopSupport {
      height: 70px;
    }
    .navTop {
      height: 70px;
      overflow: hidden;
      width: 100%;
      margin-bottom: 20px;
      position: fixed;
      top: 0;
      background-color: white;
      line-height: 70px;
      z-index: 333;
      .navLetf {
        opacity: 0.9;
        float: left;
        img {
          vertical-align: middle;
        }
        span {
          font-size: 1.2rem;
          font-weight: 600;
          margin-left: 16px;
          color: #494949;
        }
      }
      .navRight {
        opacity: 0.6;
        line-height: 70px;
        position: absolute;
        right: 5%;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .songMess {
      height: 100%;

      div.left {
        height: 100%;
        width: 100%;
        img {
          height: 54%;
          float: left;
          vertical-align: middle;
          border-radius: 10px;
          margin-right: 10px;
        }
        div.centen {
          height: 54%;
          overflow: hidden;
          position: relative;
          color: #868686;
          div {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            text-align: left;
          }
          h3 {
            font-size: 1rem;
            color: #4b4b4b;
          }
          span {
            font-size: 0.8rem;
          }
          p {
            position: absolute;
            font-size: 2rem;
            top: 50%;
            right: 0;
            transform: translateY(-56%);
          }
        }
      }
    }
  }
  .content {
    background-color: white;
    padding: 3%;
    width: 100%;
    box-sizing: border-box;
    padding-top: 10px;
    .isFixed {
      position: fixed;
      background-color: white;
      top: 70px;
      height: 40px;
      width: 94%!important;
    }
    div.top {
      width: 100%;
      margin-bottom: 20px;
      h4 {
        color: #4b4b4b;
        float: left;
      }
      p {
        text-align: right;
        span {
          margin-left: 20px;
          color: #8f8f8f;
          &.active {
            color: #3b3b3b;
            font-weight: 600;
          }
        }
      }
    }
    div.userList {
      padding-bottom: 22px;
      .item {
        padding-bottom: 14px;
        border-bottom: 1px solid rgba(100, 100, 100, 0.1);
        .userName {
          height: 40px;
          margin-top: 14px;
          img.left {
            height: 40px;
            border-radius: 50%;
            float: left;
          }
          div.nickname {
            height: 40px;
            margin-left: 10px;
            float: left;
            line-height: 20px;
            p {
              font-size: 0.9rem;
              text-align: left;
              color: #777676;
            }
            span {
              font-size: 0.7rem;
              text-align: left;
              color: #999898;
              display: block;
            }
          }
          .give {
            float: right;
            height: 22px;
            margin-top: 8px;
            background-image: url(../assets/images/icon/give.png);
            background-size: 22px 22px;
            background-repeat: no-repeat;
            text-align: right;
            padding-right: 24px;
            background-position: right;
            i {
              font-style: normal;
              text-align: right;
              color: #999898;
              font-size: 0.8rem;
            }
          }
        }
        .comment {
          text-align: left;
          font-size: 1rem;
          margin-left: 50px;
          color: #414141;
          font-weight: 400;
          line-height: 26px;
        }
      }
    }
  }
  .write {
    width: 100%;
    height: 50px;
    background-color: white;
    border-top: 1px solid rgba(100, 100, 100, 0.1);
    position: fixed;
    bottom: -1px;
    z-index: 233;
    input {
      height: 50px;
      list-style: none;
      border: none;
      background-color: #414141;
      width: 80%;
      float: left;
      text-align: left;
      background-color: white;
      padding-left: 20px;
      box-sizing: border-box;
      outline: none;
    }
    button {
      width: 20%;
      height: 50px;
      float: right;
      border: none;
      background-color: white;
      color: rgb(138, 138, 138);
      text-align: right;
      padding: 14px;
    }
  }
}
</style>
<script>
import Loading from "@/components/Loading";
export default {
  name: "Discuss",
  components: { Loading },
  // props: ["id"],
  data() {
    return {
      isLoading: true,
      data: [],
      indexActive: 1,
      commentsUser: [],
      hotCommentsUser: [],
      searchBarFixed: false,
    };
  },
  mounted() {
    let discuss=document.querySelector('.discuss');
    // console.log(discuss);
    discuss.addEventListener("scroll", this.handleScroll);
  },
  created() {
    // console.log(this.$route.query.id);
    this.$axios
      .get(this.$route.query.url + this.$route.query.id + "&limit=100")
      .then((data) => {
        return data.data;
      })
      .then((item) => {
        // console.log(item);
        this.data = item.comments;
        this.commentsUser = item.comments;
        this.isLoading = false;
        if (item.hotComments != undefined) {
          this.hotCommentsUser = item.hotComments;
        }
      });
  },
  filters: {
    formatTime(val) {
      let d = new Date(val);
      return (
        d.getFullYear() + "年" + (d.getMonth() + 1) + "月" + d.getDate() + "日"
      );
    },
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      var offsetTop = document.querySelector(".content").offsetTop;
      if (scrollTop > offsetTop) {
        this.searchBarFixed = true;
      } else {
        this.searchBarFixed = false;
      }
      // console.log(scrollTop,offsetTop)
    },
  },
};
</script>
